

import Slider from '@mui/joy/Slider';
import React from 'react';



interface MiniteSliderPros{
    sliderOrientation?: "horizontal" | "vertical" | undefined;
    valueText:string;
}

const MiniteSlider: React.FC<MiniteSliderPros> = ({ sliderOrientation="vertical" ,valueText}) => {
  return (
   <Slider  aria-label="Small steps"
   defaultValue={0.00000005}
   getAriaValueText={valueText}
   step={0.00000001}
   marks
   min={-0.00000005}
   max={0.0000001}
   valueLabelDisplay="auto"/>
  );
};

export default MiniteSlider;